<template>
  <div class="search">
    <h1 style="font-size: 25px">相关投票信息</h1>
    <div class="show" style="margin-right: 60px">
      <h4>输入待查询的dao，如aave、uniswap等等</h4>
    </div>
    <div class="show">
      <el-tooltip placement="right">
        <div slot="content">
          aave<br />aavegotchi<br />badgerdao<br />banklessdao<br />bitdao<br />citydao<br />compound<br />decentraland<br />dxdao<br />ens<br />gitcoin<br />lido<br />matic<br />moondao<br />mutantcatsdao<br />opendao<br />pancakeswap<br />radicle<br />rarible<br />synthetix<br />thelao<br />uniswap<br />wonderland
        </div>
        <el-button>more</el-button>
      </el-tooltip>
    </div>
    <el-input
      v-model="input"
      placeholder="请输入内容"
      class="search-input"
      type="text"
    >
      <i slot="prefix" class="el-input__icon el-icon-search"></i>
    </el-input>
    <el-button icon="el-icon-search" circle @click="change"></el-button>
    <div class="dashboard" style="width: 100%; height: 100%"></div>
  </div>
</template>

<script>
export default {
  name: "change",
  data() {
    return {
      input: "",
    };
  },
  methods: {
    change() {
      console.log(this.input);
      if (this.input == "aave") {
        this.$router.push("/aave");
      } else if (this.input == "Aavegotchi" || this.input == "aavegotchi") {
        this.$router.push("/Aavegotchi");
      } else if (this.input == "BadgerDAO" || this.input == "badgerdao") {
        this.$router.push("/BadgerDAO");
      } else if (this.input == "BanklessDAO" || this.input == "banklessdao") {
        this.$router.push("/Bankless DAO");
      } else if (this.input == "BitDAO" || this.input == "bitdao") {
        this.$router.push("/BitDAO");
      } else if (this.input == "CityDAO" || this.input == "citydao") {
        this.$router.push("/CityDAO");
      } else if (this.input == "Compound" || this.input == "compound") {
        this.$router.push("/Compound");
      } else if (this.input == "Decentraland" || this.input == "decentraland") {
        this.$router.push("/Decentraland");
      } else if (this.input == "DXdao" || this.input == "dxdao") {
        this.$router.push("/DXdao");
      } else if (this.input == "ENS" || this.input == "ens") {
        this.$router.push("/ENS");
      } else if (this.input == "Gitcoin" || this.input == "gitcoin") {
        this.$router.push("/Gitcoin");
      } else if (this.input == "Lido" || this.input == "lido") {
        this.$router.push("/Lido");
      } else if (this.input == "MATIC" || this.input == "matic") {
        this.$router.push("/MATIC");
      } else if (this.input == "MoonDAO" || this.input == "moondao") {
        this.$router.push("/MoonDAO");
      } else if (
        this.input == "MutantCatsDAO" ||
        this.input == "mutantcatsdao"
      ) {
        this.$router.push("/MutantCats DAO");
      } else if (this.input == "OpenDAO" || this.input == "opendao") {
        this.$router.push("/OpenDAO");
      } else if (this.input == "PancakeSwap" || this.input == "pancakeswap") {
        this.$router.push("/PancakeSwap");
      } else if (this.input == "Radicle" || this.input == "radicle") {
        this.$router.push("/Radicle");
      } else if (this.input == "Rarible" || this.input == "rarible") {
        this.$router.push("/Rarible");
      } else if (this.input == "Synthetix" || this.input == "synthetix") {
        this.$router.push("/Synthetix");
      } else if (this.input == "THE LAO" || this.input == "thelao") {
        this.$router.push("/THE LAO");
      } else if (this.input == "uniswap") {
        this.$router.push("/uniswap");
      } else if (this.input == "Wonderland" || this.input == "wonderland") {
        this.$router.push("/Wonderland");
      } else {
        alert("请输入正确的组织名");
      }
    },
  },
};
</script>

<style>
.show {
  display: inline-block;
}
</style>